.repos__heading {
  font-size: 32px;
}

.repos__list {
  min-height: 700px;
  list-style: none;
  padding: 0;
  margin: 29px 0 25px 0;
}

.repo-item {
  width: 100%;
  min-height: 112px;
  margin-bottom: 25px;
  background: #FFFFFF;
  border-radius: 6px;
}

.repo-item__inner {
  padding: 24px 32px 8px 24px;
}

.repo-item__name {
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 16px;
  color: #0064EB;
}

.repo-item__name a {
  text-decoration: none;
}

.repo-item__description {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

@media (max-width: 550px){
  .repos__heading{
    font-size: 28px;
  }
}

@media (max-width: 340px){
  .repos__heading{
    text-align: center;
  }
}
